import apiNameModel from '../models/modelName';

export default async function updateapiName(fileNameList) {
  const apiName = apiNameModel();
  await Promise.all(
    fileNameList.map(async (modelName) => {
      return apiName.findByIdAndUpdate(
        { _id: modelName._id },
        modelName.update
      );
    })
  );

  return { message: 'apiName updated successfully' };
}
